<template>
	<div id="app">
		<div style="height: 60px;line-height: 60px;background-color: lime;padding: 0 200px;">
			<div style="height: 60px;padding: 0 20px;float: left;">
				<a href='#/'>index</a>
			</div>
			<div style="height: 60px;padding: 0 20px;float: left;">
				<a :href="'#/playlist/'+$store.state.surlmid+'/'+$store.state.surlname"> playlist </a>
			</div>
		</div>

		<div style="min-height: 300px;padding: 0 200px;">
			<keep-alive><router-view></router-view></keep-alive>
		</div>

		<div style="height: 200px;background-color: lime;">
			<audio :src="$store.state.songurl" autoplay controls></audio>
		</div>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
export default {
	name: 'app',
	data() {
		return {
			
		};
	}
};
</script>

<style>
* {
	margin: 0;
	padding: 0;
}
a {
	text-decoration: none;
	color: #000;
}
</style>



