<template>
	<div id="index">
		<div class="search">
			<input type="text" v-model="value" @keyup.enter="abc">
		</div>
		<div class="search_listbox">
			<div class="search_list"></div>
			<div v-if="songlist">
				<div v-for="i in songlist">
					<a :href="'#/playlist/'+i.songmid+'/'+i.songname" @click="surl(i)" :style="{color:i.songmid==$store.state.surlmid?'red':''}">{{i.songname}}</a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	export default {
		data(){
			return {
				value:'',
				songlist:[]
			}
		},
		methods:{
			surl(a){
				this.$http.jsonp('https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg?g_tk=1278911659&hostUin=0&format=jsonp&callback=callback&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0&cid=205361747&uin=0&songmid='+a.songmid+'&filename=C400'+a.songmid+'.m4a&guid=3655047200',{callback:'callback'}).then((data)=>{
					this.$store.state.songurl = 'http://dl.stream.qqmusic.qq.com/'+data.data.data.items[0].filename+'?vkey='+data.data.data.items[0].vkey+'&guid=3655047200&fromtag=66'
					this.$store.state.surlmid = a.songmid
					this.$store.state.surlname = a.songname
				})
			},
			abc(){
				this.$http.jsonp('https://c.y.qq.com/soso/fcgi-bin/client_search_cp?g_tk=5381&p=1&n=20&w='+this.value+'&format=jsonp&jsonpCallback=callback&loginUin=0&hostUin=0&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0&remoteplace=txt.yqq.song&t=0&aggr=1&cr=1&catZhida=1&flag_qc=0',{jsonpCallback:'callback'}).then((data)=>{
					this.songlist = data.data.data.song.list
				})
			}
		}
	}
</script>

<style>
	.search{
		height: 40px;
		padding: 10px 0;
	}
	.search input{
		width: 100%;
		height: 40px;
	}
	.search_listbox{
		min-height: 50px;
	}
	.search_list{
		height: 50px;
		line-height: 50px;
	}
</style>



