import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import index from './c/index.vue'
import playlist from './c/playlist.vue'

Vue.use(Router)
Vue.use(Resource)

export default new Router({
	routes:[
		{
		  path:'/',
		  name:'index',
		  component:index
		},
		{
		  path:'/playlist/:songmid/:songname',
		  name:'playlist',
		  component:playlist
		}
	]
})


